// 通过mutation间接更新state的多个方法的对象
import {reqAddress, reqCategorys, reqShops,reqUser,
reqLogout,
reqShopGoods,
reqShopRatings,
reqShopInfo,
reqSearchShop} from '../api'
import {RECEIVE_ADDRESS, RECEIVE_CATEGORYS, RECEIVE_SHOPS,RECEIVE_USERINFO,
RESET_USERINFO,
RECEIVE_GOODS,
RECEIVE_RATINGS,
RECEIVE_INFO,
INC,DEC,CC,R_S_S} from './mutation-types'
export default{
async getAddress({commit, state}) {
const geohash = state.latitude + ',' + state.longitude
const result = await reqAddress(geohash)
commit(RECEIVE_ADDRESS, {address: result.data})
},
// 异步获取分类列表
async getCategorys({commit}) {
const result = await reqCategorys()
commit(RECEIVE_CATEGORYS, {categorys: result.data})
},
// 异步获取商家列表
async getShops({commit, state}) {
const {latitude, longitude} = state
const result = await reqShops({latitude, longitude})
commit(RECEIVE_SHOPS, {shops: result.data})
},
//同步获取用户信息
recordUser({commit},userInfo){
	commit(RECEIVE_USERINFO,{userInfo})
},
//
async getUserInfo({commit}){
	const result=await reqUser()
	if(result.code==0){
		const userInfo = result.data
		commit(RECEIVE_USERINFO,{userInfo})
	}
	
},
//异步登出
async lg({commit}){
	const result=await reqLogout()
	if(result.code==0){
		commit(RESET_USERINFO)
	}
	
},
async getShopGoods({commit},callback){
	const result=await reqShopGoods()
	if(result.code==0){
		const goods = result.data
		commit(RECEIVE_GOODS,{goods})
		callback && callback()
	}
},
async getShopInfo({commit}){
	const result=await reqShopInfo()
	if(result.code==0){
		const info = result.data
		commit(RECEIVE_INFO,{info})
	}
},
async getShopRatings({commit},callback){
	const result=await reqShopRatings()
	if(result.code==0){
		const ratings = result.data
		commit(RECEIVE_RATINGS,{ratings})
		callback && callback()
	}
},
updateFoodCount({commit},{isAdd,food}){
	if(isAdd){
		commit(INC,{food})
    }else{
		commit(DEC,{food})
	}

},
clearCart({commit}){
	commit(CC)
},
  async searchShops({commit, state}, keyword) {

    const geohash = state.latitude + ',' + state.longitude
    const result = await reqSearchShop(geohash,keyword)
    if (result.code === 0) {
      const searchShops = result.data
      commit(R_S_S, {searchShops})
    }
  },
}


