/* eslint-disable no-new */
Vue.use(VueLazyload,{
// 	preLoad:1.3,
// // 	error:'dist/error.png',
// 	loading:'dist/loading.gif',
	// attemp:1
	loading
})
new Vue({	
  el: '#app',
	// render:h=>h(App),
  components: { App },
  template: '<App/>',
	router,
	store
})



